import React from "react";
import Layout from "./../components/Layout/Layout";

const About = () => {
  return (
    <Layout title={"About us - Ecommer app"}>
      {/* upar prop pass karaya hai */}
      <div className="row contactus ">
        <div className="col-md-6 ">
          <img
            src="/images/about.jpeg"
            alt="contactus"
            style={{ width: "100%" }}
          />
        </div>
        <div className="col-md-4">
          <p className="text-justify mt-2">
          Discover a world of convenience and endless choices with our online store. 
          Explore a diverse range of products right at your fingertips, from the latest gadgets to fashion trends and home essentials. Immerse yourself in a seamless shopping experience, complete with user-friendly navigation, secure transactions,
           and swift doorstep delivery. With our website, shopping transforms into an effortless journey, making your every click a step closer to fulfilling your desires.
          </p>
        </div>
      </div>
    </Layout>
  );
};

export default About;
