import React, { useState, useEffect } from "react";
import Layout from "./../../components/Layout/Layout.js";
import AdminMenu from "./../../components/Layout/AdminMenu.js";
import toast from "react-hot-toast";
import axios from "axios";
import CategoryForm from "../../components/Form/CategoryForm.js";
import { useAuth } from "../../context/auth";
import {Modal} from 'antd'

const CreateCategory = () => {
  const [auth] = useAuth();
  const [categories, setCategories] = useState([]);
  const [name, setName] = useState("");
  const [visible,setVisible] = useState(false);
  const [selected,setSelected] = useState(null);
  const [updateName,setUpdateName] = useState("");

  //handle submit of form
  const handleSubmit = async (e) => {
    e.preventDefault();
    console.log("Submit is clicked.");
    try {
      const {data}  = await axios.post(
      `${process.env.REACT_APP_API}/api/v1/category/create-category`,{ name },{
        headers: {
         Authorization: `${auth.token}`,
        },
      }

      );
     
      if (data?.success) {
        toast.success(`${name} is created`);
        getAllCategories();
         // Reset the form input after successful submission
      setName(""); // Set the name state to an empty string
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      console.log(error);
      toast.error("somthing went wrong in input form");
    }
  };

  //get all categories
  const getAllCategories = async () => {
    try {
      const { data } = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/category/get-category`
      ); //yha request jayegi
      if (data.success) {
        setCategories(data.category);
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong in getting categories.");
    }
  };

  //ab categories ko fetch krate hai
  useEffect(() => {
    getAllCategories();
  }, []);

  //update Category
  const handleUpdate = async(e)=>{
    e.preventDefault();
    try {
      const {data} = await axios.put(`${process.env.REACT_APP_API}/api/v1/category/update-category/${selected._id}`,{name:updateName},{
        headers: {
          Authorization: `${auth.token}`, //passing token for verification.
         },
      })
      if(data?.success){
        toast.success(`${updateName} is updated.`);
        setSelected(null)
        setUpdateName("")
        setVisible(false)//submit karte hi popup close ho jaye
        getAllCategories();
      }else{
        toast.error(data.message);
      }
    } catch ( error) {
      toast.error("Something went wrong.")
    }
  }  ;

  //Delete Category
  const handleDelete = async(pId)=>{
    try {
      const {data} = await axios.delete(`${process.env.REACT_APP_API}/api/v1/category/delete-category/${pId}`,{
        headers: {
          Authorization: `${auth.token}`, //passing token for verification.
         },
      })
      if(data.success){
        toast.success(`${name} is deleted.`);
        getAllCategories();
      }else{  
        toast.error(data.message);
      }
    } catch ( error) {
      toast.error("Something went wrong.")
    }
  }  

  return (
    <Layout title={"Dashboard - Create Category"}>
      <div className="container-fluid m-3 p-3">
        <div className="row">
          <div className="col-md-3">
            <AdminMenu />
          </div>
          <div className="col-md-9">
            <h1>Manage Category</h1>
            <div className="p-3 w-50">
              <CategoryForm
                handleSubmit={handleSubmit}
                value={name}
                setValue={setName}
              />
            </div>
            <div className="w-75">
              <table className="table">
                <thead>
                  <tr>
                    <th scope="col">Name</th>
                    <th scope="col">Action</th>
                  </tr>
                </thead>
                <tbody>
                  {categories?.map((c) => (  // har category name ko c represent kar rha hai
                    <>
                      <tr>
                        <td key={c._id}>{c.name}</td>
                        <td>
                          {/* {" "} */}
                          <button className="btn btn-primary ms-2" onClick = {()=> {setVisible(true) ; setUpdateName(c.name);setSelected(c)}}>Edit</button>
                          <button className="btn btn-danger ms-2" onClick = {() => {handleDelete(c._id)}} >Delete</button>
                        </td>
                      </tr>
                    </>
                  ))}
                </tbody>
              </table>
            </div>
            <Modal onCancel = {() => setVisible(false)} footer = {null} visible = {visible}>  
               <CategoryForm value = {updateName} setValue={setUpdateName} handleSubmit={handleUpdate} />
            </Modal>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default CreateCategory;
